body {
  margin: 0;
  background-image: url(/natural_pic/static/media/fondo.51c21046.png);
  background-size: cover;
}

.App {
  font-family: "Maitree", serif;
  text-align: center;
  color: #43a047;
  
}

h1 {
  margin-bottom: 0;
}

.centrado{
  display: flex;
  justify-content: center;
}
.centrar{
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: white;
}

.navbar2 {
  background: #43a047;
  padding: 20px;
  color: white;
}

.caja {
  width: 270px;
  height: 190px;
  position: relative;
  margin: 20px 0px 10px 0px;

}

.caja img {
  object-fit: cover;
  width: 100%;
  position: relative;
  
}

.heart {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  font-weight: 900;
}
.texto_blanco{
  color:white;
  font-size: 10pt;
  font-weight: 900;
  position: absolute;
  left: 0; 
  bottom: 0;
  text-align: left;
}
